import React, {PureComponent } from "react";
import { GET_NETWORK_COLOR } from "../../../../utils/Network";
import ReactHtmlParser from "react-html-parser";
export default class Author extends PureComponent {

  render() {
    const { author, authorClass, networkId, size } = this.props;

    return <div key={Math.random()} className={authorClass} style={{ overflow: networkId == 4 ? 'visible' : '' }}>
              <img className={`${authorClass}__`} src={author.picture} aria-label="author" alt={ReactHtmlParser(author.name)} width={size ? size : 44} height={size ? size : 44}
              onError={(e) => {
                const networkColor = GET_NETWORK_COLOR(networkId); 
                const background = networkColor == '#ffffff' ? '000' : networkColor.replace("#", ""); 
                e.target.src = author.errorPic ? author.errorPic : `https://ui-avatars.com/api/?name=${author.name.replace(/\s/g, '')}&background=${background}&color=fff&length=1`}}
              />
          </div>
  }
}

